'use strict';

var buttonsList = document.querySelector('.abonements__switches');
var buttons = document.querySelector('.abonements-switches__button');
var tariffs = document.querySelectorAll('.abonements__tariffs');

buttonsList.addEventListener('click', function (evt) {
  var target = evt.target;

  if (target.classList.contains('abonements-switches__button')) {

    console.dir(target);
    tariffs.forEach(function (item) {
      if (!item.classList.contains('hidden')) {
        item.classList.add('hidden');
      }
    });
  }

});
